@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700");

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  line-height: 24px;
  font-size: 14px;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  color: #777777;
}

a {
  text-decoration: none;
  transition: all 0.3s ease-in-out;

  &:hover,
  &:focus {
    outline: none;
  }
}

section {
  background: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

button:focus {
  outline: none;
}

.section_gap {
  padding: 120px 0;
}

.subsection_gap {
  margin: 80px 0 10px 0;
}

.narrow_gap {
  margin: 40px 0 10px 0;
}

@media (max-width: 1200px) {
  .subsection_gap {
    margin: 50px 0 5px 0;
  }

  .narrow_gap {
    margin: 25px 0 10px 0;
  }
}

.title {
  color: #222222;
  font-size: 36px;
  line-height: 45px;
  font-weight: 600;
}

.theme_btn {
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  color: #555;
  font-weight: 600;
  background: #f3c300;
  padding: 5px 30px;
  border-radius: 0px;
  position: relative;
  z-index: 1;
  transition: all 0.3s linear;
  overflow: hidden;
  border: 0px;
  display: inline-block;
  text-align: center;

  &.stretch {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }

  &:before {
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
    transform: translateX(-100%);
    z-index: -1;
    background: #f8b100;
    transition: all 0.2s linear;
    opacity: 0;
  }

  &:hover,
  &:focus {
    color: #888;
    text-decoration: underline;

    &:before,
    &:before {
      transform: translateX(0);
      opacity: 1;
    }
  }
}

.default-switch {
  width: 35px;
  height: 17px;
  border-radius: 8.5px;
  background: #fff;
  position: relative;
  cursor: pointer;
}

.default-switch input {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.default-switch input+label {
  position: absolute;
  top: 1px;
  left: 1px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #f8b600;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.default-switch input:checked+label {
  left: 19px;
}

.single-element-widget {
  margin-bottom: 30px;
}

.primary-switch {
  min-width: 35px;
  width: 35px;
  height: 17px;
  border-radius: 8.5px;
  background: #aaa;
  position: relative;
  cursor: pointer;
}

.primary-switch input {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  height: 57px;
  margin-top: -20px;
}

.primary-switch input+label {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.primary-switch input+label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  border-radius: 8.5px;
  cursor: pointer;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.primary-switch input+label:after {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #fff;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.primary-switch input:checked+label:after {
  left: 19px;
}

.primary-switch input:checked+label:before {
  background: #f8b600;
}

.confirm-switch {
  width: 35px;
  height: 17px;
  border-radius: 8.5px;
  background: #fff;
  position: relative;
  cursor: pointer;
}

.confirm-switch input {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.nice-select {
  -webkit-tap-highlight-color: transparent;
  background-color: #fff;
  border-radius: 5px;
  border: solid 1px #e8e8e8;
  box-sizing: border-box;
  clear: both;
  cursor: pointer;
  display: block;
  float: left;
  font-family: inherit;
  font-size: 14px;
  font-weight: normal;
  height: 42px;
  line-height: 40px;
  outline: none;
  padding-left: 18px;
  padding-right: 30px;
  position: relative;
  text-align: left !important;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  white-space: nowrap;
  width: auto;
}

@media (pointer: fine) {
  .nice-select:hover {
    border-color: #dbdbdb;
  }
}

.nice-select:active,
.nice-select.open,
.nice-select:focus {
  border-color: #999;
}

.nice-select:after {
  border-bottom: 2px solid #999;
  border-right: 2px solid #999;
  content: '';
  display: block;
  height: 5px;
  margin-top: -4px;
  pointer-events: none;
  position: absolute;
  right: 12px;
  top: 50%;
  -webkit-transform-origin: 66% 66%;
  -ms-transform-origin: 66% 66%;
  transform-origin: 66% 66%;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  width: 5px;
}

.nice-select.open:after {
  -webkit-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

.nice-select.open .list {
  opacity: 1;
  pointer-events: auto;
  -webkit-transform: scale(1) translateY(0);
  -ms-transform: scale(1) translateY(0);
  transform: scale(1) translateY(0);
}

.nice-select.disabled {
  border-color: #ededed;
  color: #999;
  pointer-events: none;
}

.nice-select.disabled:after {
  border-color: #cccccc;
}

.nice-select.wide {
  width: 100%;
}

.nice-select.wide .list {
  left: 0 !important;
  right: 0 !important;
}

.nice-select.right {
  float: right;
}

.nice-select.right .list {
  left: auto;
  right: 0;
}

.nice-select.small {
  font-size: 12px;
  height: 36px;
  line-height: 34px;
}

.nice-select.small:after {
  height: 4px;
  width: 4px;
}

.nice-select.small .option {
  line-height: 34px;
  min-height: 34px;
}

.nice-select .list {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 0 1px rgba(68, 68, 68, 0.11);
  box-sizing: border-box;
  margin-top: 4px;
  opacity: 0;
  overflow: hidden;
  padding: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: 0;
  -webkit-transform-origin: 50% 0;
  -ms-transform-origin: 50% 0;
  transform-origin: 50% 0;
  -webkit-transform: scale(0.75) translateY(-21px);
  -ms-transform: scale(0.75) translateY(-21px);
  transform: scale(0.75) translateY(-21px);
  -webkit-transition: all 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.15s ease-out;
  transition: all 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.15s ease-out;
  z-index: 9;
}

.nice-select .list:hover .option:not(:hover) {
  background-color: transparent !important;
}

.nice-select .option {
  cursor: pointer;
  font-weight: 400;
  line-height: 40px;
  list-style: none;
  min-height: 40px;
  outline: none;
  padding-left: 18px;
  padding-right: 29px;
  text-align: left;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.nice-select .option:hover,
.nice-select .option.focus,
.nice-select .option.selected.focus {
  background-color: #f6f6f6;
}

.nice-select .option.selected {
  font-weight: bold;
}

.nice-select .option.disabled {
  background-color: transparent;
  color: #999;
  cursor: default;
}

.no-csspointerevents .nice-select .list {
  display: none;
}

.no-csspointerevents .nice-select.open .list {
  display: block;
}

@media (max-width: 1480px) {
  .navbar .menu_nav .nav-item.submenu ul {
    left: auto;
    right: 0;
  }
}

@media(max-width: 991px) {
  .navbar-toggler {
    border: none;
    border-radius: 0px;
    padding: 0px;
    cursor: pointer;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .navbar {
    padding: 0px 15px;
  }

  .header_area .navbar-brand {
    padding-top: 0px;
  }

  .main_menu_inner {
    background: #000;
  }

  .navbar-toggler[aria-expanded="false"] span:nth-child(2) {
    opacity: 1;
  }

  .navbar-toggler[aria-expanded="true"] span:nth-child(2) {
    opacity: 0;
  }

  .navbar-toggler[aria-expanded="true"] span:first-child {
    transform: rotate(-45deg);
    position: relative;
    top: 7.5px;
  }

  .navbar-toggler[aria-expanded="true"] span:last-child {
    transform: rotate(45deg);
    bottom: 6px;
    position: relative;
  }

  .navbar-toggler span {
    display: block;
    width: 25px;
    height: 3px;
    background: #fab915;
    margin: auto;
    margin-bottom: 4px;
    transition: all 400ms linear;
    cursor: pointer;
  }

  .navbar .menu_nav .nav-item {
    z-index: 1;
  }

  .navbar .menu_nav .nav-item .nav-link {
    line-height: 36px;
    padding: 0px;
    position: relative;
  }

  .navbar .menu_nav .nav-item.dropdown.show:after,
  .navbar .menu_nav .nav-item.dropdown:focus:after {
    color: #52c5fd;
    outline: none;
  }

  .navbar .menu_nav .nav-item+li {
    margin-left: 0px;
  }

  .hotel_booking_table {
    padding: 28px 15px 15px;
  }

  .banner_area {
    display: block;
  }

  .banner_area .position {
    position: relative;
    background: #04091e;
    bottom: 0px;
  }

  .banner_area {
    min-height: 100%;
  }

  .banner_content {
    margin-bottom: 94px;
    margin-top: 124px;
  }

  .accomodation_area .row {
    max-width: 556px;
    margin: 0px auto -30px;
  }

  .about_content {
    padding-right: 0px;
  }

  .testimonial_slider .testimonial_item {
    padding: 25px 30px;
  }

  .footer-area .col-sm-6 {
    margin-bottom: 30px;
  }

  .footer-bottom .footer-social {
    text-align: center;
    margin-top: 20px;
  }

  .blog_banner .banner_content {
    margin: 200px 0px 120px;
  }

  .categories_post img {
    width: 100%;
  }

  .blog_categorie_area .col-lg-4+.col-lg-4 {
    margin-top: 40px;
  }

  .blog_area {
    padding-bottom: 80px;
  }

  .single-post-area .blog_right_sidebar {
    margin-top: 30px;
  }

  .blog_banner_two {
    padding: 135px 0px 55px;
  }
}

@media(max-width: 767px) {
  .about_content {
    padding-bottom: 50px;
  }

  .single-recent-blog-post {
    max-width: 360px;
    margin: 0px auto 30px;
  }

  .hotel_booking_table {
    display: block;
    padding: 30px 0px;
  }

  .gallery_item img {
    max-width: 100%;
    width: 100%;
  }

  .blog_banner .banner_content h4 {
    font-size: 48px;
    line-height: 58px;
    margin-bottom: 20px;
  }

  .blog_info {
    text-align: left !important;
    padding-top: 0px;
    padding-bottom: 10px;
  }

  .blog_meta li {
    display: inline-block;
    width: calc(95%/ 2);
  }

  .single-post .blog_info {
    padding-top: 30px;
    padding-bottom: 0px;
  }

  .single-post-area .social-links {
    padding-left: 0px;
    margin-bottom: 0px;
  }

  .comment-form .email {
