@media(max-width: 575px) {
  .banner_content h2 {
    font-size: 40px;
    line-height: 50px;
  }

  .banner_content p br {
    display: none;
  }

  .accomodation_area .row {
    max-width: 278px;
    margin: 0px auto -30px;
  }

  .section_gap {
    padding: 70px 0;
  }

  .section_title h2 {
    font-size: 26px;
    line-height: 36px;
  }

  .testimonial_slider .testimonial_item img {
    margin-right: 0px;
    margin-bottom: 15px;
  }

  .testimonial_slider .testimonial_item {
    display: block;
  }

  .navbar {
    padding: 0px 10px;
  }

  .header_area .container {
    padding: 0px;
  }

  .header-top .container {
    padding: 0px 15px;
  }

  .blog_banner .banner_content h4 {
    font-size: 36px;
    line-height: 46px;
  }
}

@media(max-width: 480px) {
  .comments-area .comment-list.left-padding {
    padding-left: 5px;
  }

  .comments-area .thumb {
    margin-right: 5px;
  }

  .comments-area .btn-reply {
    padding: 0px 8px;
  }
}